	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"It Takes Two","date":"29 Oct 2001","slug":"/2001-10-29-it-takes-two/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/10/crhtower.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"crhtower.jpg: \" /></p>\n<p>Have you heard about the remake of the Indiana Jones series, with a  forensic entomologist in the title role? Yes, it's <a href=\"http://iowa.weblogger.com/2001/10/29\">Iowa Jones and the Mosquito of Doom</a>!</p>"},"next":{"fields":{"title":"Moonlight Savings Time","date":"28 Oct 2001","slug":"/2001-10-28-moonlight-savings-time/"}},"previous":{"fields":{"title":"Lego Man","date":"30 Oct 2001","slug":"/2001-10-30-lego-man/"}}},{"node":{"fields":{"title":"Moonlight Savings Time","date":"28 Oct 2001","slug":"/2001-10-28-moonlight-savings-time/","tags":"Y2001"},"html":"<p><a href=\"http://www.dansanderson.com/blogtracker/\">blogtracker</a> now lets you search for blogs by name or URL, and even lets you set a time zone. Cool. </p>\n<p>I've been inspired to overhaul my favorites list here. It's mostly for my own use, but everyone is free to click on the links.</p>\n<p>Keeping Christopher up for an extra hour worked, but not as well as we had hoped. He slept one hour longer, but that hour was of course negated by the switch back to standard time.</p>\n<p>weblogs.com seems to have stumbled over the time change... the 'when' attributes was showing negative numbers for new updates in the first hour after the switch. The problem disappeared after that \";->\"</p>\n<p>We finally got confirmation from EWETel that we will be switching our phone line on 23 Nov. That's good, since our flatrate with T-Online expired two weeks ago. Until EWETel takes over, we're paying for our Internet by the minute, just like in the dark ages.</p>\n<p><a href=\"http://www.nypost.com/sports/worldseries/34488.htm\">NY Post</a>: \"The Yankees melted in the desert last night in Game 1 of the World Series. They were putrid on the mound, rotten in the field and impotent at the plate.\" <i>(via <a href=\"http://scriptingnews.userland.com/backissues/2001/10/28#l0d7ab9c1ed29596ac4000ab08ee2eeb8\">Scripting News</a>, no friend of the Yankees)</i></p>"},"next":{"fields":{"title":"Space Park","date":"27 Oct 2001","slug":"/2001-10-27-space-park/"}},"previous":{"fields":{"title":"It Takes Two","date":"29 Oct 2001","slug":"/2001-10-29-it-takes-two/"}}},{"node":{"fields":{"title":"Space Park","date":"27 Oct 2001","slug":"/2001-10-27-space-park/","tags":"Y2001"},"html":"<p>We three took a field trip to Bremen, where Mama has a professional interest in the <a href=\"http://www.space-park.bremen.de\">Space Park Bremen</a>, a edutainment/shopping complex under construction on the harbor scheduled to open next fall. But can it really attract 12 million visitors a year?</p>\n<p><a href=\"http://www.dansanderson.com/blogtracker/\">blogtracker</a> might be a good way to keep track of favorites with the new <a href=\"http://www.weblogs.com\">weblogs.com</a>. It takes a long time select entries, though, since there are some 5000 to choose from.</p>\n<p>Christopher is still waking us up way too early. Today we forced him to stay awake until 9pm, so hopefully he will let us sleep in tomorrow morning. </p>\n<p>He's fascinated by electric drills, it seems. He was cheers us on whenever we have something to drill, and of course the big loud hammer drill is better than the little rechargeable Chinese drill. And he pretends to drill into the floor, complete with sound effect. So we got him a toy tool chest today, complete with battery-powered toy drill. So he doesn't have to make his own noise anymore.</p>"},"next":{"fields":{"title":"What's that noise?","date":"25 Oct 2001","slug":"/2001-10-25-whats-that-noise/"}},"previous":{"fields":{"title":"Moonlight Savings Time","date":"28 Oct 2001","slug":"/2001-10-28-moonlight-savings-time/"}}}],"pathPrefix":"","first":false,"last":false,"index":965,"pageCount":1101}}

